import './App.css';
import {useEffect, useState} from "react";

function App() {
  const [audio] = useState(new Audio('https://allsoundsaround.com/wp-content/uploads/2021/01/zvuk-najatiya-knopki-kogda-uchastnik-ostanavlivaet-muzyiku-v-peredache-ugaday-melodiyu-5469-1.mp3?_=5'));
  const [time, setTime] = useState(false);

  useEffect(() => {

  },[])

    const getDate = () => {
      const date = new Date();
      return date.getMinutes() + ':' + date.getSeconds() + ':' + date.getMilliseconds();
    }

  return (
    <div className="App">
      <header className="App-header">
        <button
            disabled={time}
            onClick={() => {
            audio.play()
                setTimeout(()=>{
                    setTime(true)
                }, 100);
        }} className="button">ЖМИ!!!</button>
      </header>
        {time && (
            <div className="block">
                <div>
                    <div>{getDate()}</div>
                    <button onClick={() => {setTime(false)}} className="next">Играть снова</button>
                </div>
            </div>
        )}
    </div>
  );
}

export default App;
